import React from 'react';
import StripeCheckout from 'react-stripe-checkout';
import { Mutation } from 'next/router';
import Router from 'next/router';
import Nprogress from 'nprogress';
import PropTypes from 'prop-types';
import gql from 'graphql-tag';
import calcTotalPrice from '../lib/calcTotalPrice';
import Error from './ErrorMessage';
import User, { CURRENT_USER_QUERY } from './User';

function totalItems(cart)
{
	return cart.reduce((tally, cartItem) => tally + cartItem.quantity, 0);
}

const skey = "pk_test_51Gq18ZER6f7pD7T5ACGDjD9l9t4AgsJAwLdPYytTtRrVyEkeQaM952FipqF47A7gm7Kki1SN9leFDafmad5UeLtf00cUYoVKOE"


export default class TakeMyMoney extends React.Component
{
	onToken = res =>
	{
		console.log("Token Called!")
		console.log(res.id);
	}
	render()
	{
		return(
			/*User component to expose the user's cart*/
			<User>
				{({ data: { me } }) => (
					<StripeCheckout
						amount={calcTotalPrice(me.cart)}
						name="Sick Fits"
						description={`Order of ${totalItems(me.cart)} items`}
						image={me.cart[0].item && me.cart[0].item.image}
						stripeKey={skey}
						currency="USD"
						email={me.email}
						token={res => this.onToken(res)}
					>
						{this.props.children}
					</StripeCheckout>
				)}
			</User>
		);
	}
}









